/*
* Style Works for Editor Only
*/
// Icon size controls
.next-accordion-icon-control {
    position: relative;
}
.next-accordion-icon-button-control {
    text-align: right;
    position: absolute;
    right: 0px;
    bottom: 30px;
}
.next-accordion-icon-button-control .components-button:focus:not(:disabled) {
	box-shadow: none;
	outline: none;
}
.components-button-group.next-accordion-icon-button-control-units button {
    padding: 0 3px;
    box-shadow: none;
	font-size: 12px;
	color: #6e6969;
}
.components-button-group.next-accordion-icon-button-control-units.is-\%-active button.is-\%,
.components-button-group.next-accordion-icon-button-control-units.is-em-active button.is-em,
.components-button-group.next-accordion-icon-button-control-units.is-px-active button.is-px{
	color: #007cba;
}
